body {
  display: flex;
  flex-direction: column;
  font-family: 'Merriweather', serif;
  font-size: 14px;
  font-weight: 400;
  color: $dark;
  background-color: $bg-color;
  min-height: 100vh;
}

.main {
  flex: 1;
}

.container {
  max-width: 1246px;
  margin: 0 auto;
  padding: 0 15px;
}

.title {
  padding: 45px 0 39px;
  font-weight: bold;
  font-size: 24px;
  line-height: 150%;
}

.btn {
  color: $white;
  width: 122px;
  height: 48px;
  font-weight: 700;
  background-color: $btn-bg;
  transition: 0.2s;

  &--added {
    position: relative;
    padding-left: 23px;
    background-color: #5b3a32;
    &::before {
      position: absolute;
      content: '';
      width: 20px;
      height: 20px;
      top: 50%;
      transform: translateY(-50%);
      left: 8px;
      background-image: url('../img/check.svg');
    }
  }

  &:hover {
    background-color: #776763;
  }
}
