.header {
  border-bottom: 1px solid $border-color;
  &__container {
    display: flex;
    align-items: center;
    min-height: 96px;
  }
  &__logo {
    margin-right: 48px;
  }

  &__form {
    display: flex;
    max-width: 416px;
    width: 100%;
    margin-left: auto;
  }

  &__input {
    flex: 1;
    padding: 0 16px;
    border: 1px solid $border-color;
    &::placeholder {
      color: #9f9f9f;
    }
  }
}

.nav {
  &__list {
    display: flex;
    padding-right: 10px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 48px;
    }
  }
}
