.gallery {
  margin-bottom: 40px;
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 32px;
  }
}

.item {
  max-width: 280px;
  border: 1px solid $border-color;
  &--soldout {
    opacity: 0.5;
  }

  img {
    border-bottom: 1px solid $border-color;
  }

  &__descr {
    padding: 20px 24px 24px 24px;
  }

  &__title {
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;
    margin-bottom: 22px;
  }

  &__buy {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__old-price {
    font-weight: 300;
    font-size: 14px;
    line-height: 150%;
    color: #a0a0a0;
    text-decoration: line-through;
  }

  &__new-price {
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
  }

  &__soldout {
    margin-top: 34px;
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
  }

  &__btn {
    width: 118px;
  }
}
