.footer {
  color: #555;
  background-color: #eceaea;
  &__container {
    display: flex;
    align-items: center;
    min-height: 96px;
  }

  &__logo {
    margin-right: 48px;
    overflow: hidden;
    border-radius: 3%;
  }
}

.contacts {
  display: flex;
  margin-left: auto;
}
.contact {
  position: relative;
  padding-left: 24px;
  &:not(:last-child) {
    margin-right: 48px;
  }

  &::before {
    position: absolute;
    content: '';
    top: 2px;
    left: 0;
    width: 16px;
    height: 16px;
  }

  &--phone {
    &::before {
      background-image: url('../img/phone.svg');
    }
  }
  &--adress {
    &::before {
      background-image: url('../img/adress.svg');
    }
  }
}
